*,
*::before,
*::after {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
}

html,
body {
   width: 100%;
   height: 100%;
}

body {
   font-weight: 500;
   background-color: lightgray;
   font-size: 1rem;
   line-height: 1.5rem;
   color: #2C4058;
}

.container {
   width: 90%;
   max-width: 1000px;
   margin: 0 auto;
}

a,
button {
   transition: all .2s ease;
}

a {
   text-decoration: none;
   color: inherit;
}

ol,
ul {
   list-style: none;
}

img {
   vertical-align: top;
}